#wrapper {
  width: 1000px;
  height: 500px;
  margin: 40px auto;
  background: linear-gradient(#00ccff, #c14fff);
  border-radius: 25px;
  border: 2px solid #000;
  padding: 10px;
  overflow: hidden;
}
#dateCitySearch {
  width: 100%;
  height: 50px;
  margin: 10px 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
form {
  height: 120px;
  display: flex;
  flex-direction: column;
  padding: 20px 0;
  width: 150px;
}
input {
  height: 30px;
  font-size: 1.1em;
  border: 0;
  border-bottom: 2px solid #1d454f;
  background-color: transparent;
  outline: none;
  color: #1d454f;
}
button {
  height: 30px;
  margin: 10px 0;
  outline: none;
  border-radius: 5px;
  border: #1d454f;
  cursor: pointer;
  color: #000;
  font-size: 1.08em;
  border: 1px solid #1d454f;
  background-color: #5eaabd;
}
#dateCitySearch #city {
  font-size: 2em;
  color: #1d454f;
}
#dateCitySearch #date {
  color: #1d454f;
  font-size: 1.1em;
}
button:hover {
  background-color: #416770;
  transition: 0.2s;
  color: #fff;
  border: 1px solid #fff;
}
button:hover i {
  transition: 0.2s;
  color: #fff;
}
#weatherIco {
  display: flex;
  justify-content: center;
  margin: 25px 69px 0 0;
}
#temp {
  font-size: 2em;
}
#tempDiv {
  display: flex;
  justify-content: space-around;
  margin: 25px 65px 0 0;
}
i {
  font-size: 2.2em;
}
#minTemp,
#maxTemp {
  font-size: 1.1em;
}
#humidFeelsPresWindspd {
  display: flex;
  justify-content: space-around;
  margin: 50px 0 0 0;
  font-size: 1.1em;
}
#sun {
  display: flex;
  justify-content: space-around;
  margin: 60px 0 0 0;
  font-size: 1.1em;
  text-align: center;
}
#clock {
  width: 200px;
  height: 200px;
  background: transparent;
  margin: 89px auto;
  border-radius: 18px 18px 0 0;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  border-left: 2px solid #000;
  border-top: 2px solid #000;
  border-right: 2px solid #000;
  box-shadow: 0px -4px 10px 1px #fff;
  transition: 1s;
  padding: 25px 0 0 0;
}
#time {
  font-size: 2em;
}
button i {
  font-size: 0.9em;
  color: #000;
}
i {
  cursor: default;
}

@media only screen and (max-width: 768px) {
  #wrapper {
    width: 100%;
    height: 90vh;
  }
  #clock {
    margin: 500px auto;
  }
}

@media only screen and (max-width: 540px) {
  #clock {
    margin: 200px auto;
  }
}

@media only screen and (max-width: 414px) {
  #clock {
    margin: -40px auto;
  }
  form {
    width: 90px;
  }
  #tempDiv {
    width: 100%;
    margin: 40px auto;
  }
  #weatherIco {
    margin: 0px auto;
  }
  #sun {
    margin: 150px auto;
  }
  #humidFeelsPresWindspd {
    text-align: center;
  }
}

@media only screen and (max-width: 375px) {
  #clock {
    margin: -110px auto;
  }
}

@media only screen and (max-width: 360px) {
  #clock {
    margin: -40px auto;
  }
  #sun {
    margin: 100px auto;
  }
}

@media only screen and (max-width: 320px) {
  #sun {
    margin: 70px auto;
  }
  #clock {
    margin: -50px auto;
  }
}

@media only screen and (max-width: 280px) {
  #clock {
    margin: -40px auto;
  }
  #city {
    color: rebeccapurple;
  }
}
